<template>
  <div class="kanban">
    <div class="titleHead">
      <p id="pHead">{{title}}</p>
      <p id="time">{{ time }}</p>
    </div>
    <div class="kanbanContent">
      <!--  -->
      <div class="kanbanDiv" >
        <iframe class="iframeClass" v-if="src1" :src="src1" ></iframe>
        <component v-if="component1" :is="component1" :cid="cid" ></component>
      </div>
      <div class="kanbanDiv" >
        <iframe class="iframeClass" v-if="src2" :src="src2" ></iframe>
        <component v-if="component2" :is="component2" :cid="cid" ></component>
      </div>
      <div class="kanbanDiv" >
        <iframe class="iframeClass" v-if="src3" :src="src3" ></iframe>
        <component v-if="component3" :is="component3" :cid="cid" ></component>
      </div>
      <div class="kanbanDiv" >
        <iframe class="iframeClass" v-if="src4" :src="src4" ></iframe>
        <component v-if="component4" :is="component4" :cid="cid" ></component>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import { KANBAN_CONFIG } from '@/utils/ajaxConfigure';

import deviceManage from '@/views/kanban_tl/deviceManage.vue';
import indicator from '@/views/kanban_tl/indicator.vue';
import deviceState from '@/views/kanban_tl/deviceState.vue';
import distribute from '@/views/kanban_tl/distribute.vue';
import { deviceRealLiveGetDeviceRealState } from '@/api/tl';
export default {
  name: 'kanban',
  data() {
    return {
      // pageurl: 'http://localhost:8083/bigscreen_lsjx_jx/',
      title: '',
      time: '',
      cid: '',
      src1: '',
      src2: '',
      src3: '',
      src4: '',
      component1: '',
      component2: '',
      component3: '',
      component4: '',
      pageArr: [
        'deviceState',
        'distribute',
        'indicator',
        'deviceManage',
        'marketingPage.action',
        'orderPage.action',
        'planPage.action',
        'productionPage.action',
        'comprehensive.action',
        'stockPage.action'
      ],
      lbPageArr: [],
      index: 0
    };
  },
  components: {
    // 加载未审核组件
    deviceManage,
    deviceState,
    indicator,
    distribute
  },
  mounted() {
    // http://localhost:8083/bigscreen_lsjx_jx/orderPage.action
    // 市场销售 marketingPage.action
    // 订单跟踪 orderPage.action
    // 计划管理 planPage.action
    // 生产透视 productionPage.action
    // 综合指标 comprehensive.action
    // 库存管理 stockPage.action
    setInterval(() => {
      location.reload();
    }, 1000 * 60 * 30);
  },
  created() {
    this.getDataTime();
    this.getKanban();
  },
  methods: {
    async getKanban() {
      var that = this;
      try {
        const data = await deviceRealLiveGetDeviceRealState('', {});
        if (data.code === '0000') {
          that.lbPageArr = data.rows;

          that.showPage(that.index);
          setTimeout(() => {
            that.toLb();
          }, that.lbPageArr[0].playTimeSecond * 1000);
        } else {
          // showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        // this.mainPage.tableLoading = false;
      }
      // var that = this;
      // $.ajax({
      //   type: 'POST',
      //   url: 'http://192.168.3.6:8093/bigscreen/tvPlayUrl/getTvPlayUrlListByBoardId.action',
      //   dataType: 'json',
      //   // async: false,
      //   data: {
      //     id: 1
      //   },
      //   success: function(data) {
      //     console.log(data);

      //     that.lbPageArr = data.rows;

      //     that.showPage(that.index);
      //     setTimeout(() => {
      //       that.toLb();
      //     }, that.lbPageArr[0].playTimeSecond * 1000);
      //   },
      //   error: function(err) {
      //     console.log(err);
      //   },
      //   xhrFields: {
      //     withCredentials: true
      //   }
      // });
    },
    toLb() {
      var that = this;
      if (that.lbPageArr[that.index + 1]) {
        that.index += 1;
        that.showPage(that.index);
        setTimeout(() => {
          that.toLb();
        }, that.lbPageArr[that.index].playTimeSecond * 1000);
      } else {
        that.index = 0;
        that.showPage(that.index);
        setTimeout(() => {
          that.toLb();
        }, that.lbPageArr[that.index].playTimeSecond * 1000);
      }
    },
    showPage(index) {
      var that = this;
      that.kanbanConfig = KANBAN_CONFIG[that.getUrlKeyMain('cid', that.lbPageArr[index].url)];
      that.cid = Number(that.getUrlKeyMain('cid', that.lbPageArr[index].url));

      that.title = that.lbPageArr[index].urlDetail;

      that.src1 = '';
      that.src2 = '';
      that.src3 = '';
      that.src4 = '';
      that.component1 = '';
      that.component2 = '';
      that.component3 = '';
      that.component4 = '';

      that.$nextTick(function() {
        if (Number(that.getUrlKeyMain('tv1', that.lbPageArr[index].url)) <= 3) {
          that.component1 = that.pageArr[Number(that.getUrlKeyMain('tv1', that.lbPageArr[index].url))];
        } else {
          that.src1 = that.lbPageArr[index].url.split('?')[0] + '/' + that.pageArr[Number(that.getUrlKeyMain('tv1', that.lbPageArr[index].url))];
        }

        if (Number(that.getUrlKeyMain('tv2', that.lbPageArr[index].url)) <= 3) {
          that.component2 = that.pageArr[Number(that.getUrlKeyMain('tv2', that.lbPageArr[index].url))];
        } else {
          that.src2 = that.lbPageArr[index].url.split('?')[0] + '/' + that.pageArr[Number(that.getUrlKeyMain('tv2', that.lbPageArr[index].url))];
        }

        if (Number(that.getUrlKeyMain('tv3', that.lbPageArr[index].url)) <= 3) {
          that.component3 = that.pageArr[Number(that.getUrlKeyMain('tv3', that.lbPageArr[index].url))];
        } else {
          that.src3 = that.lbPageArr[index].url.split('?')[0] + '/' + that.pageArr[Number(that.getUrlKeyMain('tv3', that.lbPageArr[index].url))];
        }

        if (Number(that.getUrlKeyMain('tv4', that.lbPageArr[index].url)) <= 3) {
          that.component4 = that.pageArr[Number(that.getUrlKeyMain('tv4', that.lbPageArr[index].url))];
        } else {
          that.src4 = that.lbPageArr[index].url.split('?')[0] + '/' + that.pageArr[Number(that.getUrlKeyMain('tv4', that.lbPageArr[index].url))];
        }
      });
    },
    // 看板时间
    getDataTime() {
      this.dataTime = new Date($.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date')).getTime();
      console.log('时间', $.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date'));
      this.toTime();
      this.toGetTime();
    },
    toGetTime() {
      var that = this;
      setInterval(() => {
        // var date = new Date(this.dataTime);
        that.toTime();
      }, 1000 * 30 * 60);
    },
    toTime() {
      var date = new Date();
      var y = date.getFullYear();
      var M = date.getMonth() < 9 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1);
      var d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      // var h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
      // var m = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
      // var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
      // this.time = y + ' 年 ' + M + ' 月 ' + d + ' 日   ' + h + ':' + m + ':' + s;
      this.time = y + ' 年 ' + M + ' 月 ' + d + ' 日   ';
      this.dataTime = date.getTime();
    },
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || [, ''])[1].replace(/\+/g, '%20')) || null;
    }
  }
};
</script>

